<template>
  <div>
    <div class="vld-parent">
      <loading :active.sync="isLoading"></loading>
    </div>

    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in orders" :key="item.num">
          <td>{{ item.create_at }}</td>
          <td>{{ item.user.name }}</td>

          <td>{{ item.message }}</td>
          <td>{{ item.total | currency }}</td>
          <td>
            <span v-if="item.is_paid" class="text-success"></span>
            <span v-else></span>
          </td>
        </tr>
      </tbody>
    </table>

    <Pagination :pages="pagination" @emitPagination="getOrders" />
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "./Pagination";

export default {
  name: "Dashboard_Order",
  data() {
    return {
      orders: [],
      pagination: {},
      fullPage: true
    };
  },
  methods: {
    getOrders(page = 1) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/orders?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(res => {
        console.log(res);
        vm.orders = res.data.orders;
        vm.isLoading = false;
        vm.pagination = res.data.pagination;
      });
    }
  },
  components: {
    Pagination
  },
  created() {
    this.getOrders();
  }
};
</script>
